@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@200;400;700&family=Poppins:wght@400;700&display=swap');

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  font-family: Poppins;
  min-height: 100vh;
  width: 100%;
  background-image: linear-gradient(aqua,blue);
  display: flex;
  justify-content: center;
  align-items: center;
}
.app{
  background-color: rgb(252, 252, 252);
  width: 50vw;
  min-height: 100vh;
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  align-items: center;
  position: relative;
  gap:1rem;
}
header{
  background-color: black;
  color:white;
  width: 100%;
  height: 50px;
  position: sticky;
  top: 0;
  left: 0;
  text-align: center;
}
main{
  /* background-color: burlywood; */
  width: 100%;
  min-height: 100vh;
  max-height: auto;
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  align-items: center;
}
.empty{
  margin-top: 35vh;
}
footer{
  background-color: black;
  color:white;
  width: 100%;
  height: 60px;
  position: sticky;
  bottom: 0;
  left: 0;
  display: grid;
  place-content: center;
}
.addForm, .searchForm{
  width: 100%;
  height: 40px;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
}
.addForm input,.searchForm input{
  height: 100%;
  width: 70%;
  outline: none;
  padding: 10px;
  border: none;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  background-color: rgba(0, 0, 0,0.5);
  color: white;
  font-size: 1.2rem;
  word-spacing: 2px;

}
.searchForm input{
  width: 80%;
  border-radius: 10px;
}
.addForm label, .searchForm label{
  position: absolute;
  left: -999999px;
}

.addForm button{
  width: 10%;
  height: 40px;
  border: none;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  display: grid;
  place-content: center;
  background-color: rgba(0, 0, 0,0.8);
  color:white;
}
.addForm button:hover{
  background-color: black;
}
::placeholder{
  font-size: 0.9rem;
  color: white;
  font-weight: bold;
  letter-spacing: 1px;
}
.container{
  width: 100%;
  height: 100%;
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  align-items: center;
  gap: 1rem;
}
.item{
  width: 80%;
  min-height: 50px;
  display:flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
  background-color: rgba(0, 0, 0,0.2);
  gap: 0.5rem;
  border: 1px solid black;
  padding: 10px;
  border-radius: 10px;
  list-style: none;
}
.item label{
  width: 80%;
  text-align: center;
  height: auto;
  overflow-wrap: break-word;
}
.item input{
  width: 20px;
  height: 20px;
}
.item svg{
  color: rgb(75, 57, 240);
  font-size: 30px;
}
.item svg:hover{
  color: red;
}
@media (width<=576px){
  .app{
    width: 90vw;
  }
}
@media (width<=768px){
  .app{
    width: 90vw;
  }
}
@media (width<=992px){
  .app{
    width: 80vw;
  }
}
